@import url(https://fonts.googleapis.com/css2?family=Nosifer&display=swap);
body{
  background-color: #E06D06;
}

@media only screen and (min-width: 360px) {
  .emoji{
    font-size: 100%;
    position: absolute;
    left: 7%;
    top:60%;
    height: 40%;
    width:95%;
    display:grid;
    grid-template-columns: repeat(10 ,1fr);
    border: 2px solid #252627;
    border-radius: 5px;
    background-color: #2D2E2E;
    overflow: hidden;
    overflow-y: scroll;
  }
  #search{
    font-family: 'Nosifer', cursive;
    position: absolute;
    left: 7%;
    color: azure;
    top: 50%;
    height:5%;
    width: 94%;
    border: 2px solid #252627;
    border-radius: 5px;
    background-color: #2D2E2E;
  }
  .display{
    background-color: #2D2E2E;
    font-size: 120%;
    position: absolute;
    left: 7%;
    top: 35%;
    height: 10%;
    border: 2px solid #252627;
    border-radius: 5px;
    width: 95%;
    padding-top: 2px;
    overflow: hidden;
    overflow-y: auto;
  }
  .clear{  
    position: absolute;
    left: 100%;
    top: 31%;
    height:8%;
    width: 8%; 
  }
    
  .head{
    font-family: 'Nosifer', cursive;
    font-size: 200%;
    font-weight: 800;
    position: absolute;
    left: 30%;
    top: 8%;
  }
}
